import { videoData } from '../index';

describe('WordPress Analytics', () => {
  it('is defined', async () => {
    expect(videoData).toBeDefined();
  });

  // test case for for getting the highest time all the time
  it('will always display the highest currentTime', () => {
    videoData({
      currentTime: 10,
    });
    const result2 = videoData({
      currentTime: 9,
    });
    expect(result2.video_current_time).toBe(10);
  });

  // test case for checking duration
  it('video_current_time will never be greater that duration', () => {
    const result = videoData({
      currentTime: 100,
      duration: 10,
    });
    expect(result.video_current_time).toBe(10);
  });

  // test case for video provider
  it('video_provider will never be null if null it will be html5', () => {
    const result = videoData({
      provider: '',
    });
    expect(result.video_provider).toBe('html5');
  });

  // test case for video url
  it('video_url will be same as player.source', () => {
    const result = videoData({
      source: 'https://www.youtube.com/watch?v=CH50zuS8DD0&t=7s',
    });
    expect(result.video_url).toBe('https://www.youtube.com/watch?v=CH50zuS8DD0&t=7s');
  });

  // test case for video title
  it('video_title will be same as player.config.title', () => {
    const result = videoData({
      config: { title: 'testvideo1.mp4' },
    });

    expect(result.video_title).toBe('testvideo1.mp4');
  });
});
